import React, { useState } from "react";
import { Grid } from "@mui/material";
import Brand from "../../components/brand/brand.component";
import SimpleList from "../../components/simpleList/simpleList.component";
import {
  RecipeList,
  IRecipeList,
} from "../../components/recipeList/recipeList";
import TAGS from "../../tags_data";
import RDATA from "../../recommendations_data";
import { RecipeDetails } from "../../components/recipeDetail/recipeDetail";

const Home = () => {
  const [val, setVal] = useState("");

  const handleChange = (str: string) => {
    setVal(str);
  };

  return (
    <Grid container>
      <Grid item>
        <Brand />
      </Grid>
      <Grid item>
        <SimpleList
          tabs={[
            {
              label: "Tags",
              component: <RecipeList recipeList={TAGS as IRecipeList[]} />,
            },
            {
              label: "Recomendations",
              component: (
                <div style={{ textAlign: "left" }}>
                  <div
                    style={{
                      textAlign: "left",
                      marginLeft: "20px",
                      paddingLeft: 0,
                    }}
                  >
                    Recipe List
                  </div>
                  <SimpleList
                    width="150px"
                    scrollable
                    tabs={RDATA.map((tag) => {
                      return {
                        label: tag.recipe_name,
                        component: (
                          <div style={{ textAlign: "left" }}>
                            <div
                              style={{
                                textAlign: "left",
                                marginLeft: "20px",
                                paddingLeft: 0,
                                marginTop: "-46px",
                              }}
                            >
                              Recommendations
                            </div>
                            <SimpleList
                              width="150px"
                              scrollable
                              onClick={handleChange}
                              tabs={
                                RDATA.filter((r) => r === tag).map((r) =>
                                  r.similar_recipes.map((d) => {
                                    const l = TAGS.filter(
                                      (t) => t.recipe_name === val
                                    )[0];

                                    const ll = TAGS.filter(
                                      (t) => t.recipe_name === tag.recipe_name
                                    )[0];

                                    return {
                                      label: d,
                                      component: (
                                        <RecipeDetails
                                          name={
                                            l ? l.recipe_name : ll.recipe_name
                                          }
                                          id={l ? l.recipe_id : ll.recipe_id}
                                          sweetnessLevel={
                                            l
                                              ? l.sweetness_level
                                              : ll.sweetness_level
                                          }
                                        />
                                      ),
                                    };
                                  })
                                )[0]
                              }
                            />
                          </div>
                        ),
                      };
                    })}
                  />
                </div>
              ),
            },
            {
              label: "Settings",
              component: (<></>) as React.ReactNode,
            },
          ]}
        />
      </Grid>
    </Grid>
  );
};

export default Home;
