import {
  FormControl,
  Grid,
  InputAdornment,
  InputLabel,
  OutlinedInput,
  Typography,
} from "@mui/material";
import { useState } from "react";
import SimpleList from "../simpleList/simpleList.component";
import { RecipeDetails } from "../recipeDetail/recipeDetail";
import SearchIcon from "@mui/icons-material/Search";
export interface IRecipeList {
  recipe_name: string;
  recipe_id: string;
  sweetness_level: string;
}

export interface RecipeListProps {
  recipeList: IRecipeList[];
}

export const RecipeList = ({ recipeList }: RecipeListProps) => {
  const [search, setSearch] = useState("");
  const [rList, setRList] = useState(recipeList);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setSearch(e.target.value);
    setRList(
      recipeList.filter(
        (r) =>
          r.recipe_name.toLowerCase().includes(e.target.value.toLowerCase()) ||
          r.recipe_id.toLowerCase().includes(e.target.value.toLowerCase())
      )
    );
  };

  return (
    <>
      <Grid container sx={{ mt: -3, justifyContent: "flex-start" }}>
        <Grid item xs={4}>
          <Typography variant="h6" component="h2">
            Recipe List
          </Typography>
        </Grid>
        <Grid item xs={8} sx={{ mb: 4, mt: -1 }}>
          <FormControl sx={{ m: 1, width: "25ch" }} variant="outlined">
            <InputLabel size="small" htmlFor="outlined-adornment">
              Search
            </InputLabel>
            <OutlinedInput
              id="outlined-adornment"
              size="small"
              type="text"
              value={search}
              onChange={handleChange}
              endAdornment={
                <InputAdornment position="end">
                  <SearchIcon />
                </InputAdornment>
              }
              label="Password"
            />
          </FormControl>
        </Grid>
      </Grid>
      <Grid container xs={12} sx={{ mt: 4 }}>
        <Grid item>
          <SimpleList
            width="200px"
            scrollable
            tabs={rList.map((recipe) => ({
              label: recipe.recipe_name,
              component: (
                <RecipeDetails
                  name={recipe.recipe_name}
                  id={recipe.recipe_id}
                  sweetnessLevel={recipe.sweetness_level}
                />
              ),
            }))}
          />
        </Grid>
      </Grid>
    </>
  );
};
